





























import Vue from "vue";
import { DocPager } from "./doc.interfaces";
import { DocService } from "./doc.service";

export default Vue.extend({
  props: {
    id: {
      type: String,
      required: true
    }
  },
  data() {
    return {
      pager: undefined as (DocPager | undefined)
    };
  },
  watch: {
    id: {
      immediate: true,
      handler(val) {
        this.renderPager();
      }
    }
  },
  methods: {
    async renderPager() {
      this.pager = undefined;
      this.pager = await DocService.getPager(this.id);
    }
  }
});
